@import url(http://fonts.googleapis.com/earlyaccess/notosansjapanese.css);

$primary-green: rgb(88, 137, 87);
$background-green: #cdd5cb; //that colour format consistency
$background-brown: #3d2b13;
$primary-brown: #4b3d1a;
$secondary-brown: #6b6149;
$button-shadow: 0px 0px 25px 0px rgba(61, 48, 25, 0.55);
$panel-shadow: 0px 0px 30px 0px rgba(61, 48, 25, 0.6);
body{
  font-family: 'Noto Sans Japanese', sans-serif;
  margin: 0;
}


.banner{
  background-image: url('images/bannerimage.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 50px;

  header{
    padding: 30px 0;

    .sitename{
      font-weight: 200;
      font-size: 28px;
    }

    nav{
      float: right;

      li{
        display: inline;
        padding-left: 15px;
        list-style: none;
      }

      a{
        font-weight: 500;
        font-size: 18px;
      }
    }

    //general link styles
    a{
      text-decoration: none;
      color: white;
    }
  }//header end

  .content{
    min-width: 410px;
    max-width: 410px;
    margin: 0 auto;

    h1, h3{
      text-align: center;
      color: white;
      font-weight: 200;
    }

    h1{
      font-size: 30px;
      .enhance-weight{
        font-weight: 300;
      }
    }



    h3{
      font-size: 22px;
      margin: 45px 0;
    }

    .button-container{
      text-align: center;
      display: flex;
      justify-content: space-around;
      margin-bottom: 30px;
      .button{
        font-weight: 500;
        font-size: 18px;
      }
    }

  }

}


.info-panels{
  background-color: $background-green;
  width: 100%;
  padding-bottom: 100px;
  .wrapper-wide{
    display: flex;
    justify-content: space-around;
    .info-panel{
      display: inline-block;
      background-color: white;
      padding: 0 10px 30px 10px;
      border-radius: 10px;
      width: 30%;
      max-width: 450px;
      font-weight: 200;
      text-align: center;
      margin-top: -30px;
      box-shadow: $panel-shadow;
      h1{
        font-size: 30px;
        font-weight: 200;
        color: $primary-brown;
      }

      p{
        font-size: 20px;
        color: $secondary-brown;
      }
    }
  }
}

.useless-brown-section{
  background-color: $background-brown;
  text-align: center;
  padding-top: 60px;
  padding-bottom: 60px;
  h1{
    font-weight: 200;
    font-size: 30px;
    margin-top: 0;
    margin-bottom: 20px;
    color: white;
  }

  p{
    font-weight: 200;
    font-size: 20px;
    color: #d3d2d1;
  }
}

.call-to-action{
  background-color: $background-green;
  padding-top: 40px;
  padding-bottom: 60px;
  .wrapper-narrow{
    text-align: center;
    h1{
      font-size: 30px;
      margin-top: 0;
      margin-bottom: 60px;
      font-weight: 200;
      color: $primary-brown;
      em{
        font-weight: 500;
      }
    }

    p{
      padding-top: 60px;
      color: rgb(98, 152, 133);
      font-weight: 200;
      font-size: 18px;
    }
  }
}


.button{
  padding: 7px 50px 12px 50px;
  border-radius: 30px;
  text-decoration: none;
  color: white;
  box-shadow: $button-shadow;
}

.button-small{
  padding: 5px 15px 8px 15px;
  border-radius: 40px;
  text-decoration: none;
  color: white;
  box-shadow: $button-shadow;
}

.button-large{
  padding: 12px 75px 17px 75px;
  border-radius: 30px;
  text-decoration: none;
  color: white;
  font-weight: 500;
  font-size: 22px;
  box-shadow: $button-shadow;
}

.primary{
  background-color: $primary-green;
    &:hover{
          background-color: rgb(78, 127, 77);
    }
  }



.secondary{
  background-color: rgba(255, 255, 255, 0.25);
    &:hover{
    background-color: rgba(255, 255, 255, 0.20)
  }
}


.wrapper-wide{
  max-width: 1536px;
  width: 80%;
  margin: 0 auto;
}

.wrapper-narrow{
  width: 30%;
  max-width: 576px;
  margin: 0 auto;
}

.clearfix:before, .clearfix:after{
  content: " ";
  display:table;
}

.clearfix:after{
  clear:both;
}
